// @flow

import React from 'react';

import { SchemaDocsView } from './components/SchemaDocsView';
import { Schema } from './model';

import introspectionQuery from './introspectionQuery.txt';

export class GraphQLDocs extends React.Component {
    props: {
        url: string;
        token: string;
    };

    state: {
        schema: Schema;
    };

    componentWillMount() {
        this.fetch(this.props.url, introspectionQuery).then(json => {
            this.setState({
                schema: new Schema(json.data.data),
            });
        });
    }

    render() {
        if (this.state && this.state.schema) {
            return <SchemaDocsView schema={this.state.schema} />;
        } else {
            return <div />;
        }
    }

    fetch(url, query) {
        return axios({
            method: 'post',
            url: url,
            headers: {'Authorization': this.props.token},
            data: query
        });
    }
}



